<script>
import { defineComponent, h } from 'vue'
// import { } from '../../mixins'
import { addMixinPrefix } from '../../util/helpers'

export default defineComponent({
  name: 'Fieldset',
  mixins: [
    ...addMixinPrefix([
    ], 'fieldset')
  ],
  props: ['label', 'modelValue'],
  setup(props, { slots }) {
    const defaultSlot = () => {
      return slots.default && slots.default() || ''
    }
    // const defaultSlot = slots.default && slots.default() || ''
    const labelSlot = () => slots.label && slots.label() || ''
    return {
      defaultSlot,
      labelSlot
    }
  },
  render() {
    const childrens = [
      h( 'legend', { class: 'fieldset__label' }, this.labelSlot() ),
      this.defaultSlot()
    ]
    const listClass = ['fieldset']
    return h('fieldset', {
      class: listClass
    }, childrens)
  }
})
</script>